import ShapeGlyph from './ShapeGlyph'
/* Subclasses should contain rules to draw specific glyphs, while basegraph handles logistic of drawing and tracking */

class CellGlyph extends ShapeGlyph {
  constructor (
    layer,
    id,
    name = '',
    options = {
      ...CellGlyph.baseOptions(),
      membraneFraction: 0.5,
      membraneSize: '',
      numPoints: 300,
      spikeHeight: 0.3,
      meshType: 'grid',
      patternSize: 5, // size of pattern elements in patterning
      patternType: 'circle',
      protrusionProportion: 0.85,
      protrusionBackgroundColor: '#F5F5F5',
      protrusionStrokeColor: '#212121'
    }) {
    super(layer, id, name, options)
    this.glyphElements = CellGlyph.elements
    this.itemIds = {
      ...this.itemIds,
      membrane: null,
      spikes: null,
      protrusion: null,
      mesh: null
    }
  }

  static get type () {
    return "CellGlyph"
  }

  static get settings () {
    return {name: 'drawNucleus', message: 'Draw nucleus', options: ['Yes', 'No']}
  }

  static get shapes () {
    return {main: 'Cell', children: ['Nucleus']}
  }

  static get elements () {
    // cell elements (same as shape glyph)
    let elements = []
    for (let element of ShapeGlyph.elements) {
      element.target = 'Cell'
      elements.push(element)
    }
    // nuclear elements
    for (let element of ShapeGlyph.elements) {
      element.target = 'Nucleus'
      elements.push(element)
    }
    return elements
  }

  draw (options) {
    options.shapeType = 'ellipse' // Fixed elliptic shape for cell and nucleus glyphs
    super.draw(options)
    this.activateLayer()
    if (options.drawNucleus.toLowerCase() === 'yes') {
      this.box = this.getDrawingBox(options) // update box info
      const nucleusOptions = {
        backend: 'paper',
        strokeColor: '#283593',
        primaryColor: '#AB47BC',
        secondaryColor: '#D4E157',
        lightColor: '#1E88E5',
        darkColor: '#B71C1C',
        strokeWidth: 2,
        thickPathSize: 7,
        narrowPathSize: 4,
        numSides: 6, // for RegularPolygon: defaults to hexagon
        numPoints: 250, // for drawing Membrane and Spikes
        spikeHeight: 0.3,
        meshType: 'grid'
      }
      const positions = {
        widthProportion: 1,
        heightProportion: 1,
        leftShift: 0,
        topShift: 0
      } // Height and Width scaling is done inside ShapeGlyph
      // const protrusionScaleOrder = options.scaleOrders.find(
      //     scaleOrder => scaleOrder.element === 'Protrusion' && scaleOrder.shape === 'Cell'
      // )
      // if (protrusionScaleOrder) {
      //   positions.topShift += 0 // total height of glyph (main shape + protrusion) remains unvaried //FIXME actually it's exceeding bounding box
      //   positions.leftShift += 0
      //   positions.widthProportion *= this.parameters.protrusionProportion
      //   positions.heightProportion *= this.parameters.protrusionProportion
      // }
      options.shapePositions['Nucleus'] = positions
      const nucleus = new ShapeGlyph(this.layer, '0', 'Nucleus', nucleusOptions, this)
      nucleus.draw(options)
      this.registerChild(nucleus)
    }
  }
}

export default CellGlyph
